import type { ImageData } from '../../../mo/models';

export interface IMetaImageProps {
  data: ImageData;
  imgSrc: string; // 实际渲染的图片地址
  onStatusChange: (status) => void;
  isMobile?: boolean;
  style?: React.CSSProperties;
  onImgRef: React.ForwardedRef<HTMLImageElement>;
  onWrapperRef: React.ForwardedRef<HTMLSpanElement>;
  disableLoading: boolean; // 禁用加载态，适用于 SSR 场景
  forceLoading: boolean; // 强制加载态
  onClick?: React.MouseEventHandler;
  onContextMenu?: React.MouseEventHandler;
  onDoubleClick?: React.MouseEventHandler;
  onMouseDown?: React.MouseEventHandler;
  onTouchStart?: React.TouchEventHandler;
  onTouchMove?: React.TouchEventHandler;
  onTouchEnd?: React.TouchEventHandler;
}

export enum Status {
  Success = 'success',
  Loading = 'loading',
  Error = 'error',
}

export interface IImageProps extends IMetaImageProps{
  isAutoFit: boolean;
}
